""
import React from 'react';
import { motion } from 'framer-motion';

function Abouthome() {
  const fadeIn = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.8 } }
  };

  return (
    <section className="relative py-20 px-4 sm:px-6 lg:px-8 bg-[#0a0a0a] overflow-hidden">
      {/* Decorative elements */}
      <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-transparent via-[#c5fb45] to-transparent opacity-20"></div>
      
      <div className="max-w-7xl mx-auto">
        {/* Animated heading */}
        <motion.h2 
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
          variants={fadeIn}
          className="text-4xl md:text-5xl font-bold text-center mb-16 text-[#c5fb45] tracking-tight"
        >
          About <span className="italic">KRUNAL&apos;S</span> ACADEMY
        </motion.h2>

        {/* Content with staggered animation */}
        <div className="max-w-4xl mx-auto text-center space-y-8">
          <motion.p 
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
            variants={fadeIn}
            className="text-lg md:text-xl text-gray-300 leading-relaxed"
          >
            Founded in 2010, <span className="text-[#c5fb45] font-medium">KRUNAL&apos;S ACADEMY</span> has been at the forefront of beauty education, empowering thousands of students to become successful professionals in the industry.
          </motion.p>

          <motion.p 
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
            variants={{ ...fadeIn, transition: { delay: 0.2 } }}
            className="text-lg md:text-xl text-gray-300 leading-relaxed"
          >
            Our mission is to provide <span className="text-[#c5fb45]">world-class training</span> in hair, skin, makeup and nail artistry, combining traditional techniques with modern innovations.
          </motion.p>

          <motion.p 
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
            variants={{ ...fadeIn, transition: { delay: 0.4 } }}
            className="text-lg md:text-xl text-gray-300 leading-relaxed"
          >
            With <span className="text-[#c5fb45]">state-of-the-art facilities</span> and industry-expert instructors, we create an environment where creativity and professionalism thrive.
          </motion.p>
        </div>

        {/* Stats section */}
        <motion.div 
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true }}
          variants={{ ...fadeIn, transition: { delay: 0.6 } }}
          className="mt-16 grid grid-cols-2 md:grid-cols-4 gap-8 text-center"
        >
          {[
            { value: "13+", label: "Years Experience" },
            { value: "2K+", label: "Students Trained" },
            { value: "50+", label: "Industry Experts" },
            { value: "98%", label: "Success Rate" }
          ].map((stat, index) => (
            <div key={index} className="p-6 rounded-lg bg-[#1a1a1a] border border-[#2a2a2a]">
              <div className="text-3xl font-bold text-[#c5fb45]">{stat.value}</div>
              <div className="mt-2 text-sm uppercase tracking-wider text-gray-400">{stat.label}</div>
            </div>
          ))}
        </motion.div>
      </div>

      {/* Bottom decorative element */}
      <div className="absolute bottom-0 left-0 w-full h-1 bg-gradient-to-r from-transparent via-[#c5fb45] to-transparent opacity-20"></div>
    </section>
  );
}

export default Abouthome;