import React from 'react'

function Courseoffhome() {
  return (
 <section style={{
          padding: '4rem 2rem',
          backgroundColor: '#0a0a0a'
        }}>
          <h2 style={{
            fontSize: '2.5rem',
            textAlign: 'center',
            marginBottom: '3rem',
            color: '#c5fb45'
          }}>Our Course Offerings</h2>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
            gap: '2rem',
            maxWidth: '1200px',
            margin: '0 auto'
          }}>
            {[
              {
                title: 'Professional Makeup Artistry',
                duration: '3 Months'
              },
              {
                title: 'Advanced Hair Styling',
                duration: '2 Months'
              },
              {
                title: 'Skincare Specialist',
                duration: '6 Weeks'
              },
              {
                title: 'Nail Technician',
                duration: '4 Weeks'
              }
            ].map((course, index) => (
              <div key={index} style={{
                backgroundColor: '#1a1a1a',
                padding: '2rem',
                borderRadius: '8px',
                borderBottom: '4px solid #c5fb45'
              }}>
                <h3 style={{
                  fontSize: '1.5rem',
                  marginBottom: '1rem',
                  color: '#c5fb45'
                }}>{course.title}</h3>
                <p style={{ marginBottom: '1.5rem' }}>Duration: {course.duration}</p>
                <button style={{
                  padding: '0.5rem 1rem',
                  backgroundColor: '#c5fb45',
                  color: '#0a0a0a',
                  border: 'none',
                  borderRadius: '4px',
                  fontWeight: 'bold',
                  cursor: 'pointer'
                }}>View Details</button>
              </div>
            ))}
          </div>
        </section>  )
}

export default Courseoffhome